*{margin:0;padding:0;box-sizing:border-box}html{font-size:62.5%}body{font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;font-size:1.6rem;background:#242582;color:#949ca5;min-height:100vh;letter-spacing:1px}ul,ol{margin-left:1rem}.header{padding:2rem;display:flex;justify-content:center;align-items:center;margin-bottom:2rem}.header .title{font-weight:600;font-size:3rem;color:#fff}.search-section{width:70%;margin:20px auto}.search-section input{padding:2rem;font-size:1.5rem}.search-section .btn-search{font-size:2.2rem;letter-spacing:1px;width:13rem;line-height:1.5;color:#fff;border:none}.filters{display:flex;width:70%}.filters .filter-btn{margin:2rem;margin-left:0;padding:1rem;display:block;border:1px solid #888;border-radius:25px;cursor:pointer}.filters .active{background:#0069d9;color:#fff}.filters .full-time-checkbox input{margin-top:0.5rem}.filters .full-time-checkbox label{margin-left:1rem}.search-results{width:90%;margin:0 auto}.search-results .job-item{display:flex;padding:1rem;border:1px solid #000;border-radius:0.5rem;margin:2rem;cursor:pointer}.search-results .job-item>div{margin-left:2rem}.search-results .job-item:hover{box-shadow:0 0 10px rgba(255,255,255,0.4)}.search-results .job-item .job-info .job-title{margin-bottom:2rem}.search-results .job-item .company-logo img{max-width:10rem;height:10rem;border-radius:50%}.search-results .job-item .job-info{flex:1 1}.search-results .job-item .post-info{display:flex;justify-content:center;align-items:center}.load-more{display:flex;justify-content:center;margin-bottom:5rem}.load-more button{border:none;background:#0e7bff;color:#fff;border-radius:20px;padding:10px;letter-spacing:1px;outline:none}.job-details{width:90%;margin:50px auto}.job-details .back-link a:link,.job-details .back-link a:visited{display:block;margin-bottom:1rem}.job-details .back-link a:hover,.job-details .back-link a:active{color:#fff;text-decoration:none}.job-details .title{font-weight:bold}.job-details .main-section{display:flex}.job-details .main-section .left-section{flex:2 1;margin:0;margin-right:2rem}.job-details .main-section .right-section{flex:1 1;margin:5rem 0 0 0}.job-details .main-section .right-section>div{margin-bottom:5rem}.job-details .main-section .right-section h3{margin-bottom:1rem}.job-details .main-section .right-section .company-details .company-logo{max-width:10rem;height:10rem;border-radius:50%}.job-details .main-section .right-section .company-details .company-name{margin-top:1rem}.job-details .main-section .right-section .company-details .company-url{word-break:break-all}.job-details .main-section .right-section .how-to-apply a{display:inline-block;word-break:break-all}.loading{font-size:2rem;text-align:center;font-size:2rem;color:#fff}#loader{width:100%;height:100%;position:fixed;left:0;bottom:0;z-index:99999;background:linear-gradient(rgba(0,0,0,0.5), rgba(0,0,0,0.5))}#loader .message{position:relative;font-size:20px;top:50%;left:50%;transform:translateX(-10%);color:#fff}.loader-open{overflow:hidden}.disabled{cursor:not-allowed;opacity:0.4}.errorMsg{max-width:70%;margin:0 auto;color:#f21e08;padding:1rem;letter-spacing:2px}.hide{display:none}@media only screen and (max-width: 992px){.search-section{width:80%}}@media only screen and (max-width: 768px){.search-form .row{flex-direction:column}.search-form .col{margin-bottom:2rem}.search-section .btn-search{width:100%}.job-item{flex-direction:column}.search-results .job-item .post-info{display:inline;margin:2rem 0 0 2rem}.main-section{flex-direction:column}.job-info .job-title{margin-top:1rem}.footer{grid-row:3;display:flex;justify-content:center;align-items:center;padding:1rem;background:white;border-radius:8px;border:4px solid white;font-weight:bold}}

